@import "variables";

.light {
  background-color: white;
  color: black;

  > * {
    color: black;
  }
}

.dark {
  background-color: black;
  color: white;

  > * {
    color: white;
  }
}

.App {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.MarkdownPreviewer {
  flex: 1;
}

.paper {
  color: black !important;
  background-color: rgb(207, 207, 207) !important;
}

#preview {
  box-sizing: border-box;

  img {
    max-width: 90%;
    height: auto;
  }
}
